#album-page-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#album-page-frame {
  position: relative;
}

#album-page-view {
  border: 10px solid black;
}

#album-page-frame.landscape {
  height: 595.28px;
  width: 841.89px;
}

#album-page-frame.portrait {
  height: 841.89px;
  width: 595.28px;
}

.cropped-result-image {
  position: absolute;
  top: 0px;
  left: 0px;
  max-width: 100%;
  max-height: 100%;
}

#modal-import-container {
  display: grid;
  grid-template-rows: 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 15px;
}

#modal-import-container img {
  max-width: 500px;
  max-height: 500px;
}

#controls-container {
  display: grid;
  grid-column-gap: 10px;
  grid-auto-flow: column;
  grid-auto-columns: fit-content(300px);
}



